<script lang="ts" setup>
/**
 * 
 *
 * @author libin
 * @date 20240615 11:58:57
 */

import { Ref, onMounted, ref } from 'vue'

/**  */
const status: Ref<'load' | 'flat' | 'loaded' | 'fail'> = ref('load')
/**  */
let start: number = Date.now()

/**  */
const onLoad = (): void => {
    status.value = Date.now() - start < 100 ? 'flat' : 'loaded'
}

/**  */
const onError = (): void => {
    status.value = 'fail'
}

onMounted((): void => {
    start = Date.now()
})
</script>

<template>
    <img v-bind="$attrs" :class="['image', status]" @load="onLoad" @error="onError" />
</template>

<style lang="less" scoped>
.image.load {
    filter: opacity(0);
}
.image.flat,
.image.loaded {
    filter: opacity(1);
}
.image.loaded {
    transition: opacity 0.1s linear;
}
.image.fail {
    // , ~
    filter: opacity(1);
}
</style>
