import { useMediaQuery } from '@vueuse/core'
import { ComputedRef, Ref, computed } from 'vue'

/**  */
export const useMobileOrPortrait = (): ComputedRef<boolean> => {
    /** @  */
    const isPortrait: Ref<boolean> = useMediaQuery('(orientation: portrait)')
    const isMobile: ComputedRef<boolean> = computed(() => {
        return isPortrait.value || navigator.userAgent.toLowerCase().includes('mobile')
    })
    return isMobile
}
